import React,{Component} from 'react';
import axios from 'axios';
import PropTypes from 'prop-types';
import {reactLocalStorage} from 'reactjs-localstorage';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Input from '@material-ui/core/Input';
import FileBase64 from 'react-file-base64';
import SweetAlert from 'sweetalert2-react';
import $ from 'jquery';

const styles = theme => ({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    formControl: {
      margin: theme.spacing.unit,
      minWidth: 150,
    },
    selectEmpty: {
      marginTop: theme.spacing.unit * 0,
    },
  });
class SlideTopAdd extends Component{
    constructor(props){
        super(props);
        this.state = {
            placement : "Top Slide",
            link : "",
            file : "",
            show : false
        }
    }
    

    handleChange = event => {
        this.setState({ [event.target.name]: event.target.value });
    };

    handleEditorChange = (e) => {
        this.setState({
            descriptionweb : e.target.getContent()
        }) 
    }

    submitHandler = (event) => {
        if(this.state.file !== '' && this.state.file !== undefined && this.state.file !== null){
        const payload = {
            "file" : this.state.file,
            "placement": this.state.placement,
            "link": this.state.link
        }
        let token= reactLocalStorage.get('auth');
        const AuthStr = "Bearer ".concat(token); 
        axios.post('http://13.229.249.101/dashboard/api/slide/new', payload, { headers: { Authorization: AuthStr } })
        .then((response) => {
            if(response.data.response.success === "Insert Success"){
                window.location.href='#/admin/slidetop';
            }
        })
        .catch((error) => {
            console.log(error);
        });  
        event.preventDefault();  
        }   
    }

    getFiles(files){
        if(files.type === "image/jpeg"){
            var image = files.base64;
            var img = document.createElement("img")
            img.setAttribute("src", image)
            setTimeout(
                function() {
                    if(img.height === 785 && img.width === 1600){
                        this.setState({ "file" :  files.base64 });
                    }
                    else{
                        this.setState({
                            show: true, 
                            titlealert: "Image Size Failed", 
                            textalert : "Image Size 1600px X 785px"
                        })
                        $("input[type='file']").val("");
                    }
                }
                .bind(this),
                0
            );
        }
        else{
            this.setState({
                show: true, 
                titlealert: "Image Type Failed", 
                textalert : "Image Type Must .Jpg"
            });
        }
    }

    render(){
   
        return(
            <article className="article">
            <SweetAlert
                show = {this.state.show}
                title = {this.state.titlealert}
                text = {this.state.textalert}
                onConfirm={() => 
                    this.setState({ show: false, titlealert: "", textalert : "" })
                }
            />
                <div className="box-default m-3">
                <div className="box-body py-5">
                    <div className="form-group">
                        <h6>Add Slide Top</h6>
                    </div>
                    <form  onSubmit={this.submitHandler}>
                    
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Slide Top Image</label>
                        <div className="col-md-10">
                            <FileBase64
                            multiple={ false }
                            onDone={ this.getFiles.bind(this) }
                            className="w-100"
                            />
                        </div>
                    </div>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Link</label>
                        <div className="col-md-10">
                        <Input
                            inputProps={{name: 'link'}}
                            onChange={this.handleChange}
                            value={this.state.link}
                            placeholder="Link Slide Top"
                            multiline
                            id="inputName2"
                            fullWidth
                        />
                        </div>
                    </div>
                    

                    <div className="form-group row">
                        <div className="col-md-2"></div>
                        <div className="col-md-10">
                        <Button variant="contained" className="btn-w-md" type="submit"> Submit </Button>
                        </div>
                    </div>
                    </form>

                </div>
                </div>
            </article>
        )
    }
}
SlideTopAdd.propTypes = {
    classes: PropTypes.object.isRequired,
  };
export default  withStyles(styles)(SlideTopAdd);